import LogoFondo from "../assets/img/logoT.png"

function Logo({len}){

    console.log(len)
    return(        
        <div>
            {
                !len ?
                <img className='' style={{height: '50px'}} src={LogoFondo}></img>
                :len == 'large'?
                <img className='' style={{height: '150px'}} src={LogoFondo}></img>
                :
                <img className='' style={{height: '100px'}} src={LogoFondo}></img>
            }
        </div>
    )
}


export default Logo;